(function(){angular.module("templates", []).run(["$templateCache", function($templateCache) {$templateCache.put("profcard.html","\n<div class=\"profcard\">\n  <div class=\"title {{ card.title.color }}\">\n    <span>{{ card.title.text }}</span>\n  </div>\n  <div class=\"content\" ng-class=\"{ \'hidden\': !card.content.shown }\">\n    <span>{{ card.content.text }}</span>\n  </div>\n  <div class=\"image\" ng-class=\"{ \'blur\': card.image.blurred }\" ng-transclude>\n  </div>\n</div>\n");}]);})();